#conferences {
    background-color: var(--accent);
    color: var(--light2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 80px;
}

#conferences .button {
    justify-content: flex-start;
    margin: 0;
}

#conferences .button-link:not(:hover) {
    background-color: var(--light2);
    color: var(--accent);
}
